<template>
  <div>
    <div class="ui card">
      <div v-for="option in options" :key="option.name">
        {{ option.name }} - {{ option.votes }}
      </div>
    </div>
  </div>
</template>

<script>
import { OPTIONS } from '../options';

export default {
  name: 'voting',
  data() {
    return {
      options: OPTIONS,
      msg: 'Welcome to Your Vue.js App',
    };
  },
};
</script>
<style scoped>
[v-cloak] { display: none; }
</style>


